body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.recharts-default-tooltip{
  background-color:#6E8CA0;
}
.legend{
  position: absolute;
  bottom:0%;
  left:0%;
  width:100%;
}
.LegendIncrease {
  -webkit-animation: increase 1s;
          animation: increase 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes increase{
  0%{
    position: relative;
    -webkit-transform: scale(1) translate(0);
            transform: scale(1) translate(0);
  }
  100%{
    position: relative;
    -webkit-transform: scale(1.1,2) translate(0,-20%);
            transform: scale(1.1,2) translate(0,-20%);
  }
}
@keyframes increase{
  0%{
    position: relative;
    -webkit-transform: scale(1) translate(0);
            transform: scale(1) translate(0);
  }
  100%{
    position: relative;
    -webkit-transform: scale(1.1,2) translate(0,-20%);
            transform: scale(1.1,2) translate(0,-20%);
  }
}

.LegendDecrease {
  -webkit-animation: Decrease 1s;
          animation: Decrease 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes Decrease{
  0%{

    position: relative;
    -webkit-transform: scale(1.1,2) translate(0,-20%);
            transform: scale(1.1,2) translate(0,-20%);
  }
  100%{
    position: relative;
    -webkit-transform: scale(1) translate(0);
            transform: scale(1) translate(0);
  }
}
@keyframes Decrease{
  0%{

    position: relative;
    -webkit-transform: scale(1.1,2) translate(0,-20%);
            transform: scale(1.1,2) translate(0,-20%);
  }
  100%{
    position: relative;
    -webkit-transform: scale(1) translate(0);
            transform: scale(1) translate(0);
  }
}

::-webkit-scrollbar {
  width: 10px ;
}
::-webkit-scrollbar-track {
  background-color: transparent ;
}
::-webkit-scrollbar-thumb {
  background-color: #1b1c20;
  border-radius: 10px ;
  border: 6px solid transparent ;
  background-clip: content-box ;
}
